<template>
  <div class="index">
    <!-- 二级路由 -->
    <keep-alive>
      <router-view></router-view>
    </keep-alive>

    <van-tabbar v-model="active" active-color="#e4393c">
      <van-tabbar-item
        v-for="(item, index) in tabbarData"
        :key="index"
        :name="item.name"
        @click="togglePage({name: item.name}) "
      >
        <span>{{item.title}}</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? tabbarData[index].icon.active : tabbarData[index].icon.inactive"
        />
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState } = createNamespacedHelpers("indexModule");

export default {
  name: "index",

  data() {
    return {
      active: "home"
    };
  },

  computed: {
    ...mapState(["tabbarData"])
  },

  methods: {
    togglePage(o) {
      this.$router.push(o);
    }
  },
  created() {
    //  console.log(" this.active ==> ",  this.active );
    // console.log("this.$route ==> ", this.$route.name);
    //纠正底部导航的标签
    this.active = this.$route.name;
   
  }
};
</script>
<style>
.index .van-tabbar-item {
  color: black;
}
</style>
<style lang="less" scoped>
.index {
  padding-bottom: calc(~"1.33333rem");
}
</style>